@media screen and (max-width: 500px) {

  ._follows_page {
    display: block;
    padding-bottom: 70px;
    height: auto;
    position: static;
    padding-top: 60px;
    min-height: 100vh;
    background-color: var(--light-grey);
  }

  ._follows_page.dark {
    background-color: var(--dark-grey-2);
  }

  ._follows_page_content ._profile_page {
    display: none;
  }

  ._follows {
    max-width: none;
    width: 100%;
    border-radius: 0;
    max-height: none;
    overflow-y: auto;
  }

  ._header_follows {
    display: none;
  }
}
