._post_view {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
}

._post_view_content {
  height: 500px;
  width: 800px;
  display: flex;
  align-items: center;
  gap: 20px;
  transition: 0.5s;
}

._post_view_thumbnail {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
  justify-content: center;
  padding: 10px;
  position: relative;
}

._post_view_thumbnail img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 1em;
}

._post_view_data {
  display: flex;
  flex-direction: column;
  width: 50%;
  max-height: 100%;
  border-radius: 0.5em;
  position: relative;
  /* overflow: hidden; */
  transition: 0.5s;
}

._post_view_fixed_header {
  position: sticky;
  top: 0;
  width: 100%;
  border-radius: 0.25em 0.25em 0 0;
  transition: 0.5s;
}

._post_view_header {
  padding: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

._post_view_header_avatar {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 0.5em;
}

._post_view_user {
  display: flex;
  align-items: center;
  font-family: product_sans, sans-serif;
  gap: 5px;
  text-decoration: none;
}

._post_view_subtitle {
  padding: 0 20px 20px 20px;
  display: flex;
  gap: 10px;
  flex-direction: column;
  transition: 0.5s;
}

._post_view_subtitle_user {
  font-weight: 800;
  cursor: pointer;
  text-decoration: none;
  font-family: product_sans, sans-serif;
  transition: 0.5s;
}

._post_view_subtitle_content {
  margin-inline-start: 5px;
  font-family: product_sans, sans-serif;
  font-weight: 100;
  margin-inline-start: 5px;
  font-size: 0.9em;
  width: 100%;
}

._post_view_date {
  font-family: product_sans, sans-serif;
  font-weight: 100;
  font-size: 0.7em;
}
