._notifications {
  width: 400px;
  height: 500px;
  border-radius: 0.5em;
  box-shadow: 0 0 5px var(--light-grey-3);
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 0;
  top: 45px;
  overflow-y: scroll;
}

._notifications.bg-dark {
  box-shadow: 0 0 5px var(--medium-grey);
}

._notifications::-webkit-scrollbar {
  display: none;
}
