@media screen and (max-width: 768px) {
  ._direct_chat {
    border-radius: 0;
    border: none !important;
    grid-template-rows: 1fr;
    /* width: 100vw; */
    max-width: none;
    max-height: none;
    width: 100%;
  }

  ._delete_messages_menu {
    position: fixed;
    top: 0;
  }

  ._direct_chat._main {
    display: none;
  }

  ._chats_area {
    /* max-width: 100vw; */
    /* width: 100vw; */
    padding: 100px 20px 80px;
    overflow-y: visible;
  }

  ._header_chat {
    position: fixed;
    top: 60px;
    width: 100vw;
    z-index: 2;
  }

  ._direct_chat ._comment {
    position: fixed;
    bottom: 70px;
  }
}
