._profile_page {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0.5s;
}

._profile_content {
  padding-top: 60px;
  width: 100%;
  min-height: 100vh;
  max-width: 800px;
  position: relative;
  transition: 0.5s;
}

._header_profile {
  width: 100%;
  height: 250px;
  position: absolute;
  object-fit: cover;
}

._profile_presentation {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin-top: 150px;
  align-items: center;
  gap: 20px;
  position: relative;
}

._profile_user_avatar {
  position: absolute;
  top: -80px;
  text-align: center;
  z-index: 10;
}

._profile_user {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

._verified_user_profile {
  color: var(--orange);
  font-size: 1.2em;
}

._profile_user_avatar h1 {
  font-weight: 300;
  font-family: product_sans, sans-serif;
  transition: 0.5s;
}

._profile_user_avatar span {
  color: grey;
  font-size: 0.8em;
  font-style: italic;
  font-family: product_sans, sans-serif;
  transition: 0.5s;
}

._avatar_profile {
  height: 150px;
  width: 150px;
  object-fit: cover;
  border-radius: 50%;
  justify-self: center;
  transition: 0.5s;
  border-width: 3px !important;
}

._profile_user_data {
  display: flex;
  font-family: product_sans, sans-serif;
  flex-direction: column;
  min-height: 50vh;
  width: 100%;
  border-radius: 0.5em;
  transition: 0.5s;
}

._profile_post_title {
  display: flex;
  align-items: center;
  padding: 20px;
}

._profile_post_title div {
  display: flex;
  gap: 10px;
  align-items: center;
  width: 100%;
  transition: 0.5s;
}

._profile_post_title svg {
  font-size: 1.2em;
  color: var(--orange);
}

._profile_post_title h2 {
  font-size: 1.2em;
  font-family: product_sans, sans-serif;
  font-weight: 100;
}

._profile_posts_view {
  background: none;
  border: none;
  font-size: 1em;
  cursor: pointer;
}
