@media screen and (max-width: 768px) {
  ._header_bar {
    display: flex;
    justify-content: space-between;
    padding: 20px;
  }

  ._notifications_area {
    position: static;
  }

  ._nav_header_bar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 20px;
    justify-content: space-evenly;
    border-top-width: 1px !important;
  }

  ._header_login {
    height: 100%;
    font-size: 1em;
    padding: 0;
    background-color: #fff;
  }
}

@media screen and (max-width: 500px) {
  ._title_header_bar.logged {
    display: none;
  }

  ._search_area {
    position: static;
  }
}
