._profile_stats_bar {
  backdrop-filter: blur(10px);
  width: 100%;
  padding-top: 120px;
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em;
  transition: 0.5s;
}

._profile_actions {
  display: flex;
  /* background-color: red; */
  justify-content: space-evenly;
  padding: 0 20px 20px;
  transition: 0.5s;
}

._profile_actions button {
  padding: 5px;
  width: 40%;
  max-width: 160px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: 0.5s;
}

._profile_stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

._profile_stats_bar_icon {
  display: flex;
  flex-direction: column;
  padding: 10px;
  justify-content: center;
  align-items: center;
  gap: 5px;
  font-size: 0.8em;
  text-decoration: none;
}

._profile_stats_bar_icon div {
  display: flex;
  align-items: center;
  gap: 5px;
  font-family: product_sans, sans-serif;
  font-size: 1.2em;
}

._profile_stats_bar_icon div svg {
  font-size: 1.2em;
}

._button_unfollow {
  color: #c12828 !important;
  background-color: #efc9c9 !important;
}
