@media screen and (max-width: 768px) {

  ._trending_post {
    flex-direction: column;
    border-radius: 1.5em;
    border-width: 1px !important;
  }

  ._trending_thumbnail {
    width: 100%;
    border-radius: 1.5em 1.5em 0 0;
  }

  ._trending_post_views {
    font-size: 0.8em;
    padding: 10px 0;
  }
}