._direct_list {
  border-radius: 0.5em;
  max-height: 100%;
  min-width: 300px;
  overflow-y: auto;
}

._direct_list_header {
  padding: 10px;
  font-family: product_sans, sans-serif;
}

._direct_list_header h4 {
  font-weight: 500;
  text-align: center;
  font-size: 0.9em;
}

._direct_list ul {
  display: flex;
  flex-direction: column;
  list-style: none;
}

._direct_list_avatar {
  border-radius: 50%;
  height: 40px;
  object-fit: cover;
  width: 40px;
}

._direct_list_li {
  display: grid;
  grid-template-columns: 40px 1fr;
  padding: 10px;
  gap: 10px;
  align-items: center;
}

._direct_list_message_data {
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-decoration: none;
  font-family: product_sans, sans-serif;
}

._direct_list_message_data {
  display: flex;
}

._direct_list_message {
  display: flex;
  gap: 5px;
  font-size: 0.7em;
  align-items: center;
}

._direct_list_message span {
  color: var(--orange);
}

._direct_list_message p {
  color: var(--light-grey-3);
  max-width: 200px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
