._selected {
  background-color: #cddfff !important;
}

._not_selected {
  opacity: 0.5 !important;
}

._tick_selected {
  height: 15.35px;
  width: 15.35px;
  display: flex;
  align-items: center;
  justify-content: center;
}

._number_selected {
  animation: growIcon 0.3s;
  background-color: #5783d9;
  font-size: 0.8em;
  padding: 2px 10px;
  border-radius: 25px;
  top: -8px;
  color: #fff;
}

._own ._number_selected {
  position: absolute;
  right: 0;
  /* color: var(--dark-blue); */
}

._received ._number_selected {
  position: absolute;
  left: 0;
  /* color: var(--dark-blue); */
}
