@keyframes toTop {
  0% {
    opacity: 0%;
    transform: translateY(10px);
  }
  100% {
    opacity: 100%;
    transform: translateY(0);
  }
}

._direct_chat {
  height: 100%;
  border-radius: 0.5em;
  overflow: hidden;
  display: grid;
  grid-auto-rows: min-content;
  grid-template-rows: 60px 1fr;
  width: 100%;
  max-width: 500px;
  position: relative;
}

._delete_messages_menu {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 3;
  background-color: #ffffff21;
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
}

._delete_messages_area {
  display: flex;
  flex-direction: column;
  /* background-color: var(--lighter-grey);  */
  border-radius: 0.5em;
  width: 250px;
  animation: toTop 0.5s;
  position: relative;
}

._delete_warning {
  font-size: 0.7em;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
}

._delete_warning span {
  color: var(--red);
  font-weight: 500;
}

._delete_messages_buttons {
  display: flex;
  flex-direction: column;
  padding: 10px;
  /* align-items: center; */
  gap: 10px;
}

._delete_messages_buttons ._actions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

._btn_delete {
  height: 30px;
  border-radius: 0.5em;
  cursor: pointer;
  font-size: 0.7em;
  color: var(--red);
  transition: 0.5s;
}

._btn_delete:not(._cancel) {
  background: none;
  border: none;
}

._btn_delete:hover {
  background-color: #4545454f;
}

._btn_delete:disabled {
  cursor: default;
  opacity: 0.2;
}

._btn_delete._cancel {
  color: #0087d4;
  position: absolute;
  bottom: -40px;
  width: 100%;
  left: 0;
  border-radius: 0.5em;
}

._header_chat {
  display: grid;
  padding: 0 10px;
  align-items: center;
  height: 60px;
  gap: 10px;
}

._direct_chat._main {
  display: flex;
  align-items: center;
  justify-content: center;
}

._header_chat._main {
  grid-template-columns: 40px 1fr;
}

._header_chat._not_main {
  grid-template-columns: 30px 40px 1fr 35px 35px 35px;
}

._chat_back {
  color: var(--dark-grey);
  height: 100%;
}

._header_chat a {
  display: flex;
  align-items: center;
  justify-content: center;
}

._avatar_chat {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 50%;
}

._chat_sender_name {
  font-family: product_sans, sans-serif;
  text-align: center;
  display: flex;
  width: 100%;
  align-items: flex-start;
  flex-direction: column;
}

._chat_sender_name a {
  text-decoration: none;
}

._chat_sender_name h3 {
  font-weight: 100;
  font-size: 1em;
}

._chat_sender_name span {
  font-size: 0.8em;
}

._chat_sender_name span a {
  color: var(--light-grey-3);
}

._chat_header_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 35px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s;
}

._icon_favorite {
  color: var(--dark-blue);
  font-size: 1.1em;
}

._icon_close {
  color: var(--red);
  font-size: 1.1em;
}

._chats_area {
  padding: 80px 20px 20px;
  display: grid;
  gap: 10px;
  min-width: 320px;
  width: 100%;
  grid-auto-rows: min-content;
  align-self: flex-end;
  max-height: 100%;
  overflow-y: auto;
  scroll-behavior: smooth;
}

._top_content_chat {
  /* background-color: red; */
  padding: 10px;
  margin: 50px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  font-size: 0.8em;
  text-align: center;
  color: var(--light-grey-3);
}

._top_content_chat p {
  max-width: 400px;
}

._top_content_chat p a {
  text-decoration: none;
  color: var(--orange);
}

._top_content_chat_image {
  position: absolute;
  bottom: 100%;
}

._top_content_chat_image img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}

._top_content_chat_image._own img {
  transform: translateX(-20px);
}

._top_content_chat_image._sender img {
  transform: translateX(20px);
}

._message {
  max-width: 280px;
  padding: 10px 20px;
  position: relative;
  display: flex;
  align-items: center;
  font-size: 0.8em;
  border-radius: 1.5em;
  user-select: none;
}

._message_avatar {
  position: absolute;
  background: none !important;
}

._message_avatar img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
}

._message_avatar._last_opacity {
  opacity: 0;
}

._message_avatar._received {
  left: -40px;
}

._message_avatar._own {
  right: -40px;
}

._message._received {
  justify-self: flex-start;
  transform: translateX(40px);
}

._message._own {
  /* background-color: var(--light-grey); */
  justify-self: flex-end;
  text-align: end;
  transform: translateX(-40px);
}

._message_content {
  color: var(--dark-grey);
  user-select: none;
}

._message_time {
  color: var(--light-grey-3);
  font-size: 0.7em;
  position: absolute;
  opacity: 0;
  transition: 0.5s;
}

._message:hover ._message_time {
  opacity: 1;
}

._message._selected ._message_time {
  opacity: 1;
}

._message._own ._message_time {
  left: -35px;
}

._message._received ._message_time {
  right: -35px;
}

._direct_chat ._comment {
  display: flex;
  height: 100%;
  border-radius: 0 0 0.5em 0.5em;
  width: 100%;
  height: 50px;
  align-items: center;
}

._direct_chat ._comment span {
  position: relative;
}

._direct_chat ._emoji_btn {
  font-size: 1.5em;
  margin: 0 20px;
  color: var(--medium-grey);
  cursor: pointer;
}

._direct_chat aside.EmojiPickerReact.epr-main {
  position: absolute !important;
  bottom: 50px;
  left: 20px;
}

._direct_chat ._comment input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  padding: 5px 10px;
  font-family: product_sans, sans-serif;
}

._direct_chat ._send_comment_btn {
  padding: 0 20px;
  height: 100%;
  background: none;
  border: none;
  color: var(--orange);
  cursor: pointer;
  font-family: product_sans, sans-serif;
  font-size: 1.5em;
}

._direct_chat ._comment button:disabled {
  color: var(--orange);
  opacity: 0.3;
  cursor: not-allowed;
  font-weight: 500;
}

._send_a_direct {
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  color: var(--light-grey-3);
}

._send_a_direct_icon {
  padding: 20px;
  /* background-color: blue; */
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--light-grey-3);
}

._send_a_direct svg {
  font-size: 3.5em;
}
