._post_bar {
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: space-between;
  /* border-bottom: 1px solid #f1f1f1; */
  transition: 0.5s;
}

._post_bar div {
  display: flex;
  gap: 20px;
}

._post_bar svg {
  font-size: 1.5em;
  cursor: pointer;
}

._post_view_post_bar {
  padding: 20px;
  transition: 0.5s;
}

._liked_post svg {
  color: #ea4040;
}

._post_data {
  padding: 10px;
  width: 100%;
  transition: 0.5s;
}

._post_data_user {
  font-weight: 800;
  cursor: pointer;
  text-decoration: none;
  transition: 0.5s;
}

._post_subtitle {
  font-family: product_sans, sans-serif;
  font-weight: 100;
  margin-inline-start: 5px;
  font-size: 0.9em;
  width: 100%;
  text-align: left;
  transition: 0.5s;
}

._post_date {
  font-family: product_sans, sans-serif;
  font-weight: 100;
  font-size: 0.7em;
  width: 100%;
  padding: 5px 10px;
  transition: 0.5s;
}

._post_comments {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: 0.8em;
  cursor: pointer;
  height: 100%;
  border-radius: 0 0 0.25em 0.25em;
  transition: 0.5s;
}

._comments_link {
  padding: 10px;
  font-family: product_sans, sans-serif;
  transition: 0.5s;
}

._post_comments ._comment {
  display: flex;
  height: 100%;
  width: 100%;
  height: 50px;
  align-items: center;
  border-radius: 0 0 0.25em 0.25em;
  transition: 0.5s;
}

._post_comments ._comment span {
  position: relative;
}

._post_comments ._emoji_btn {
  font-size: 1.5em;
  margin: 0 20px;
  cursor: pointer;
}

._post_comments aside.EmojiPickerReact.epr-main {
  position: absolute !important;
  bottom: 50px;
  left: 20px;
}

._post_comments ._comment input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  padding: 5px;
  font-family: product_sans, sans-serif;
  transition: 0.5s;
}

._post_comments ._send_comment_btn {
  padding: 0 20px;
  height: 100%;
  background: none;
  border: none;
  color: var(--orange);
  cursor: pointer;
  font-family: product_sans, sans-serif;
  font-size: 1.5em;
}

._post_comments ._comment button:disabled {
  color: var(--orange);
  opacity: 0.3;
  cursor: not-allowed;
  font-weight: 500;
}
